<template>
  <div id="app">
    <md-layout md-align="center">
      <h1 class="md-headline">Translate It</h1>
    </md-layout>
    <translateForm v-on:formSubmit="translateText"></translateForm>
    <md-layout md-align="center">
      <md-layout md-align="center">
        <translateOutput v-text="translatedText"></translateOutput>
      </md-layout>
    </md-layout>
  </div>
</template>

<script>
import translateForm from './components/translateForm';
import translateOutput from './components/translateOutput';

export default {
  name: 'app',
  components: {
    translateForm,
    translateOutput
  },
  data: () => ({
    translatedText:''
  }),
  methods: {
    translateText:function(text, language){
      this.$http.get('https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20171001T173409Z.79cb65a03377bb77.b388ed2954443c6c43149d24ec5b0a6650c141d8&lang='+language+'&text='+text)
      .then((response) => {
        this.translatedText = response.body.text[0];
      });
    }
  }
};

</script>



