<template>
  <div id="translateForm">

      <form v-on:submit="formSubmit">
        <md-layout md-row md-gutter>
        <md-layout md-gutter md-flex="60" md-flex-offset="5">
            <md-input-container>
              <md-input type="text" v-model="textToTranslate"></md-input>
              <label>What would you like to translate?</label>
            </md-input-container>
        </md-layout>
        <md-layout md-flex="15" md-flex-offset="5">
          <div class="field-group">
            <md-input-container>
              <label for="language">Language</label>
              <md-select name="language" id="language" v-model="language">
                <md-option value="da">Danish</md-option>
                <md-option value="no">Norwegian</md-option>
                <md-option value="es">Spanish</md-option>
                <md-option value="sv">Swedish</md-option>
              </md-select>
            </md-input-container>
          </div>
        </md-layout>
        <md-layout >
          <md-button class="md-fab md-primary" type="submit">
            <md-icon>send</md-icon>
          </md-button>
        </md-layout>
        </md-layout>
      </form>

  </div>
</template>

<script>

export default {
  name: 'translateForm',
  data: () => ({
    textToTranslate:'',
    language:''
  }),
  created() {
    this.language = 'es';
  },
  methods: {
    formSubmit(e){
      this.$emit('formSubmit', this.textToTranslate, this.language);
      e.preventDefault();
    }
  }
};

</script>



